@import url(https://fonts.googleapis.com/css2?family=Libre+Bodoni:wght@400;500&display=swap);
.Clock{color:#199b81;font-size:30px;margin:40px}

body{background:#66cdaa}html{font-family:"Libre Bodoni", serif}.App{max-width:600px;height:600px;margin:auto;border-radius:50%;background-color:#dae2e9;text-align:center;color:#131211;display:flex;justify-content:center;align-items:center;flex-direction:column}.App__button{margin:15px;width:200px;height:50px;font-size:15px;letter-spacing:3px;background:linear-gradient(#637471, #38aca3);border-radius:20px;border:0;color:#dae2e9;transition:0.5s}.App__button:hover{border-radius:15px;background:linear-gradient(#2a302f, #50beb5);-webkit-transform:scale(1.1);transform:scale(1.1)}

